@import url(https://fonts.googleapis.com/css2?family=Andika+New+Basic:ital,wght@0,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-top: 5vh;
  margin-bottom: 5vh;
  margin-left: 10vw;
  margin-right: 10vw;
  background-color: rgb(241, 241, 241);
  font-family: 'Andika New Basic', sans-serif;
}

@media only screen and (max-width: 900px) {
  body {
    font-size: 12px;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.react-search-field-input {
  padding: 0 !important; 
  color: rgb(90,90,90)
}

::-webkit-input-placeholder {
  color: black !important;
  font-weight: normal !important;
}

:-ms-input-placeholder {
  color: black !important;
  font-weight: normal !important;
}

::-ms-input-placeholder {
  color: black !important;
  font-weight: normal !important;
}

.react-search-field > input, ::placeholder {
  color: black !important;
  font-weight: normal !important;
}


.react-search-field {
  border: 2px solid black !important;
  border-radius: 3px !important;
}

.skylight-dialog {
  border-radius: 10px !important;
} 

#results {
  overflow: scroll;
  padding-top: 10px;
}

#results::-webkit-scrollbar {
  -webkit-appearance: none;
}

#results::-webkit-scrollbar:vertical {
  width: 11px;
}

#results::-webkit-scrollbar-thumb {
  border-radius: 8px;
  border: 2px solid white; /* should match background, can't be transparent */
  background-color: rgba(0, 0, 0, .5);
}

.optionCard {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.optionTitle {
  font-weight: bold;
}

.optionTitle p, h3 {
  margin-top: 0;
  margin-bottom: 0;
}

.optionTitle p {
  font-weight: normal;
}

.nominateButton {
  background-color: rgb(121, 255, 233);
  width: 20%;
  max-width: 100px;
  min-width: 81px;
  height: 40px;
  border: none;
  color: black;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 15px;
  border-radius: 10px;
  cursor: pointer;
}

.nominateButton:disabled {
  background-color: tomato;
}
.card {
  display: flex;
  align-items: center;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 5px 15px #669c9e;
  margin-top: 3vh;
  min-height: 50px;
  padding: 10px;
  position: relative;
  z-index: inherit;
}

.cardnum {
  width: 10%;
  text-align: center;
  border-radius: 10px;
  color: white;
  background-color: rgb(98, 235, 212);
}

.detail, .movieDetail {
  margin-left: 10px;
  align-items: center;
  cursor: pointer;
  width: 80%;
}

.movieDetail {
  cursor: auto;
}

.movieDetail > h2 {
  margin-top: 0;
  margin-bottom: 0;
}

.movieDetail > p {
  font-size: small;
  margin-top: 0;
  margin-bottom: 0;
}

.trashButton {
  position: absolute;
  text-align: end;
  right: 0;
  margin-left: 15px;
  margin-right: 15px;
  margin-bottom: 20px;
  font-size: large;
  cursor: pointer;
}

.trashButton:hover {
  color: red;
}

.submitButton {
  background-color:rgb(98, 235, 212); 
  color: white;
  border: none;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 15px;
  font-weight: bold;
  border-radius: 10px;
  width: 20%;
  height: 4vh;
  margin: auto;
  cursor: pointer;
  box-shadow: 0px 8px 15px #669c9e;
  transition: transform .2s;
  min-height: 40px;
  min-width: 92px;
}

.submitButton:hover {
  transform: scale(1.5);
}

.submitButtonContainer {
  margin-top: 3vh;
  margin-bottom: 3vh;
}

.title {
  font-size: xx-large;
}

.banner {
  z-index: 99;
  padding-top: 2vh;
  border-radius: 10px;
  top: 0;
  background-color: tomato;
  position: -webkit-sticky;
  text-align: center;
  align-items: center;
}

.about {
  overflow-y: auto;
  min-height: 300px;
}

.about::-webkit-scrollbar {
  -webkit-appearance: none;
}

.about::-webkit-scrollbar:vertical {
  width: 11px;
}

.about::-webkit-scrollbar-thumb {
  border-radius: 8px;
  border: 2px solid white; /* should match background, can't be transparent */
  background-color: rgba(0, 0, 0, .5);
}

.footer {
  font-size: x-large;
  margin-top: 5vh;
  color: black !important;
  display: flex;
  text-align: center;
  justify-content: center;
}
